.form-section {
  text-align: center;
  width: 100vw;
  z-index: 2;
  background: rgba(6, 66, 113, .9);
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: block;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
}

input {
  height: 30px;
  width: 250px;
  border: 1px solid #838181;
  border-radius: 16px;
  margin-right: 10px;
  padding: 0 15px;
  font-size: 16px;
}

.search-button {
  height: 40px;
  width: 100px;
  color: #064271;
  background-color: #57fa2e;
  border: 2px solid #57fa2e;
  border-radius: 20px;
  font-size: 16px;
  font-weight: 600;
}

.hidden {
  display: none;
}

.load-more-button-wrapper {
  text-align: center;
  justify-content: center;
  display: block;
}

.load-more-button {
  height: 45px;
  width: 175px;
  color: #fff;
  text-align: center;
  background-color: #064271;
  border: 2px solid #064271;
  border-radius: 20px;
  margin-top: 20px;
  margin-bottom: 50px;
  font-size: 20px;
  font-weight: 500;
}

.load-more-button:hover, .load-more-button:focus {
  background-color: #240671;
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 2px 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.search-button:hover, .-more-button:focus {
  background-color: #3fb821;
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 2px 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.gallery {
  grid-template-columns: repeat(4, 1fr);
  gap: 20px;
  margin: 20px;
  display: grid;
}

img {
  width: 100%;
  height: 300px;
  object-fit: cover;
  border-radius: 16px 16px 0 0;
}

.photo-card {
  border: 1px solid #838181;
  border-radius: 20px;
  box-shadow: 3px 2px 10px -1px #47425a;
}

.info {
  text-align: center;
  grid-template-columns: repeat(2, 2fr);
  justify-content: center;
  display: grid;
}

.info-item {
  border-radius: 50px;
  margin: 5px 25px 10px;
  box-shadow: 2px 2px 10px -2px #47425a;
}

